import get from 'lodash/get';

import { IPlayerContext } from '../Models';
import { checkIsBigDecimal } from '../utils';

/**
 *    .
 *     "link"     .
 */
export class ContextPath {
  //  
  isContextPath: boolean = true;

  //     
  contextPath: string = null;

  constructor(contextPath: string) {
    this.contextPath = contextPath;
  }

  /**
   *       contextName.
   *
   * @param {IPlayerContext} context   .
   */
  getValue(context: IPlayerContext) {
    let contextValue: any = get(context, this.contextPath, new Error());
    if (contextValue instanceof Error) {
      throw new Error(
        `        : "${this.contextPath}"`,
      );
    } else if (checkIsBigDecimal(contextValue)) {
      // BPSBSCP-599: Big.js        
      //            
      contextValue = +contextValue; //  Number(contextValue)
    }
    return contextValue;
  }
}
