import React, { PureComponent } from 'react';
import { IBackendError, IHistoryContext, IPlayerContext, IVersionInfo } from '../Models';
import { scriptInformation } from '../helpers/ScriptInformation';
import { Button, Col, List, Row, Space } from 'antd';

/**
 *  .
 */
export interface IProps {
  onReload: () => void;
  onExit: () => void;
  errors: IBackendError[];
  playerContext: IPlayerContext;
  history: IHistoryContext[];
  versionInfo: IVersionInfo;
}

/**
 *  .
 *
 * @prop {boolean} showReport    .
 * @prop {number} errorIndex    .
 */
export interface IState {
  showReport: boolean;
  errorIndex: number;
}

export const LABEL_BACK = '  ';
export const LABEL_EXIT = '  ';
export const LABEL_RELOAD = ' ';

/**
 *    .
 */
export class PlayerErrors extends PureComponent<IProps, IState> {
  //     IDE.
  props: IProps;

  state: IState = {
    showReport: false,
    errorIndex: null,
  };

  /**
   *   .
   */
  handleExit = () => {
    this.props.onExit();
  };

  /**
   *  .
   */
  handleReload = () => {
    this.props.onReload();
  };

  /**
   *    .
   *
   * @param {number} errorIndex  .
   */
  handleShowReport = (errorIndex: number) => () => {
    this.setState({ showReport: true, errorIndex });
  };

  /**
   *    .
   */
  handleHideReport = () => {
    this.setState({ showReport: false, errorIndex: null });
  };

  /**
   *     JSON.
   */
  renderReport = () => {
    const { errors, playerContext, history, versionInfo } = this.props;
    const { errorIndex } = this.state;
    const stringPresentation = JSON.stringify(
      {
        sessionId: versionInfo.sessionId,
        errors: errors[errorIndex],
        scriptInformation: {
          subject: scriptInformation.getSubject(),
        },
        playerContext,
        history,
      },
      null,
      2,
    );

    return (
      <div data-test-id="PlayerErrors.report">
        {this.renderAttentionMessage()}
        <pre data-test-id="PlayerErrors.report.presentation">{stringPresentation}</pre>
      </div>
    );
  };

  /**
   *     .
   */
  renderAttentionMessage = () => (
    <p data-test-id="PlayerErrors.attention.message">
      <b>!</b>       , 
           !
    </p>
  );

  /**
   *     .
   */
  renderAdditionalInfo = () => (
    <div data-test-id="PlayerErrors.AdditionalInfo">{this.renderAttentionMessage()}</div>
  );

  /**
   *   .
   */
  renderError = () => {
    return (
      <div data-test-id="PlayerErrors.errors">
        <List
          data-test-id="PlayerErrors.error.list"
          itemLayout="horizontal"
          dataSource={this.props.errors}
          bordered
          renderItem={item => (
            <List.Item data-test-id="PlayerErrors.error.item">
              <List.Item.Meta
                data-test-id="PlayerErrors.error.item.message"
                title={item.title}
                description={item.text}
              />
            </List.Item>
          )}
        />
      </div>
    );
  };

  /**
   *   .
   */
  render() {
    const { showReport } = this.state;

    return (
      <div data-test-id="PlayerErrors">
        <Row>
          <Col xs={24}>{showReport ? this.renderReport() : this.renderError()}</Col>
        </Row>
        <Row style={{ paddingTop: 10 }}>
          <Col xs={24}>{this.renderAdditionalInfo()}</Col>
        </Row>
        <Row style={{ paddingTop: 10 }}>
          <Col xs={24}>
            <Space>
              {showReport ? (
                <Button
                  data-test-id="PlayerErrors.action.hide"
                  htmlType="button"
                  onClick={this.handleHideReport}
                >
                  {LABEL_BACK}
                </Button>
              ) : (
                <Button
                  data-test-id="PlayerErrors.action.reload"
                  htmlType="button"
                  type="primary"
                  onClick={this.handleReload}
                >
                  {LABEL_RELOAD}
                </Button>
              )}
              <Button
                data-test-id="PlayerErrors.action.exit"
                htmlType="button"
                style={{ marginRight: 0 }}
                onClick={this.handleExit}
              >
                {LABEL_EXIT}
              </Button>
            </Space>
          </Col>
        </Row>
      </div>
    );
  }
}
