import escapeRegExp from 'lodash/escapeRegExp';
import get from 'lodash/get';
import uniq from 'lodash/uniq';

import { IPlayerContext } from '../Models';

/**
 *    .
 *     "template"     .
 */
export class TemplatedString {
  //  
  isTemplatedString: boolean = true;

  //  - ,  'Hello, Mr ${NAME} !{SURNAME}!'
  template: string = null;

  //     .
  contextPaths: string[] = [];

  /**
   *          .
   *
   * @param {string} template .
   */
  getContextPaths(template: string): string[] {
    const r = new RegExp(
      '[$!]{(([a-zA-Z]\\w*)((\\.[a-zA-Z]\\w*)|(\\[\'\\S+\'])|(\\[\\"\\S+\\"])|(\\[\\d+]))*)(?=})',
      'gm',
    );
    const tokens = template.match(r);

    if (tokens === null || tokens.length === 0) {
      throw new Error('  .');
    }

    //     ,        .
    //         "${example"
    return uniq(tokens).map(token => token.slice(2));
  }

  constructor(template: string) {
    this.template = template;
    try {
      this.contextPaths = this.getContextPaths(template);
    } catch (e) {
      throw new Error(`      "${template}": ${e.message}`);
    }
  }

  forReplace(char: string, token: string) {
    return new RegExp(escapeRegExp(char + '{' + token + '}'), 'gm');
  }

  /**
   *      .
   *
   * @param {IPlayerContext} context     .
   */
  getValue(context: IPlayerContext) {
    let result = this.template;
    try {
      this.contextPaths.forEach((token: string) => {
        const value = get(
          context,
          token,
          new Error(`    "${token}"    .`),
        );

        if (value instanceof Error) {
          throw value;
        }

        if (value === null) {
          throw new Error(`     "${token}"     .`);
        }

        result = result
          .replace(this.forReplace('$', token), value)
          .replace(this.forReplace('!', token), value);
      });
    } catch (e) {
      throw new Error(
        `      : "${this.template}". ${e.message}`,
      );
    }
    return result;
  }
}
