import { EErrorType } from './Enums';
import { IScriptBlockError, IError, IErrorExceptions, IWidgetError } from './Models';
import {
  ERROR_SERVER_TITLE,
  ERROR_PLAYER_TITLE,
  ERROR_SCRIPT_TITLE,
  ERROR_SCRIPT_ASYNC,
  ERROR_SCRIPT_GLOBAL,
  ERROR_CALCULATE_TITLE,
} from '_components/Const';
import { formatDate } from './utils';

/**
 *   .
 *
 * @param {IScriptBlockError} error .
 * @param {string} defaultTitle  -  .
 */
function prepareError(error: IScriptBlockError, defaultTitle: string) {
  console.error(error.title, error.text, error.exception);
  return {
    title: error.title ? error.title : defaultTitle,
    text: error.text ? error.text : '',
    dateTime: formatDate(),
  };
}

/**
 *                 BH.
 *
 * @param {IScriptBlockError} error .
 */
export function prepareServerErrorExpected(error: IScriptBlockError): IError {
  let result: IError = {
    errorType: EErrorType.SERVER_EXPECTED,
    ...prepareError(error, ERROR_SERVER_TITLE),
  };

  //    Error.
  if (error.exception) {
    result.exception = error.exception;
  }

  //       .    IRequestError.
  if (error.code) {
    result.code = error.code;
  }

  return result;
}

/**
 *              UI.
 *  BH  ,      BH.
 *
 * @param {IScriptBlockError} error .
 */
export function prepareFromServerError(error: IScriptBlockError): IError {
  return {
    errorType: EErrorType.FROM_SERVER,
    code: error.code ? error.code : '',
    system: error.system ? error.system : '',
    uuid: error.uuid ? error.uuid : '',
    ...prepareError(error, ERROR_SERVER_TITLE),
  };
}

/**
 *           BH.
 *
 * @param {IScriptBlockError} error .
 */
export function preparePlayerErrorExpected(error: IScriptBlockError): IError {
  return {
    errorType: EErrorType.PLAYER_EXPECTED,
    ...prepareError(error, ERROR_PLAYER_TITLE),
  };
}

/**
 *             BH.
 *
 * @param {IScriptBlockError} error .
 */
export function prepareBlockErrorExpected(error: IScriptBlockError): IError {
  return {
    errorType: EErrorType.BLOCK_EXPECTED,
    ...prepareError(error, ERROR_SCRIPT_TITLE),
    blockId: error.blockId ? error.blockId : '',
    additionalId: error.additionalId ? error.additionalId : '',
  };
}

/**
 *             BH.
 *
 * @param {IWidgetError} error   .
 */
export function prepareWidgetErrorExpected(error: IWidgetError): IError {
  console.error(error.title, error.text, error);
  return {
    errorType: EErrorType.WIDGET_EXPECTED,
    title: error.title ? error.title : '',
    text: error.text ? error.text : '',
    widgetInfo: error.widgetInfo,
    blockId: error.blockId ? error.blockId : '',
    additionalId: error.additionalId ? error.additionalId : '',
    dateTime: formatDate(),
  };
}

/**
 *            BH.
 *
 * @param {Error} error .
 */
export function prepareCalculateErrorExpected(
  error: Partial<IErrorExceptions> & Partial<IScriptBlockError>,
): IError {
  console.error(error.title, error.message);
  return {
    errorType: EErrorType.CALCULATE_EXPECTED,
    title: error.title ? error.title : ERROR_CALCULATE_TITLE,
    text: error.message ? error.message : '',
    exception: error.stack ? error.stack : '',
    blockId: error.blockId ? error.blockId : '',
    additionalId: error.additionalId ? error.additionalId : '',
    dateTime: formatDate(),
  };
}

/**
 *              BH.
 *
 * @param {Error} error .
 */
export function prepareAsyncErrorExpected(error: Error): IError {
  console.error(ERROR_SCRIPT_ASYNC, error.stack ? error.stack : '');
  return {
    errorType: EErrorType.GLOBAL_ASYNC,
    title: ERROR_SCRIPT_ASYNC,
    text: error.message ? error.message : '',
    exception: error.stack ? error.stack : '',
  };
}

/**
 *           BH.
 *
 * @param {ErrorEvent} error .
 */
export function prepareGlobalErrorExpected(error: ErrorEvent): IError {
  //      console.error, ..    .
  return {
    errorType: EErrorType.GLOBAL,
    title: ERROR_SCRIPT_GLOBAL,
    text: error.message ? error.message : '',
    file: error.filename ? error.filename : '',
    line: error.lineno ? error.lineno : null,
    column: error.colno ? error.colno : null,
    exception: error.error.stack ? error.error.stack : '',
  };
}

/**
 * -  
 *
 * @param {string} title  .
 * @param {string} message  .
 */
export class CustomTitleError extends Error {
  title: string;

  constructor(title, message) {
    super(message);
    this.title = title;
  }
}
