export interface IVersionInfo {
  version: string;
  distribVersion: string;
  subsystem?: string;
  channel?: string;
  deploymentUnit?: string;
  platform?: string;
  sessionId?: string;
  serverIp?: string;
  standinMode?: 'PRIMARY' | 'STANDIN' | 'BLACKOUT';
}

/**
 *    .
 *
 * @prop {string} message       .
 */
export interface IValidator extends IExpressionWithOriginal {
  message: string;
}

/**
 *    .
 *
 * @prop {string} message   .
 * @prop {IWidget | string} source  .    -.
 */
export interface IValidationError {
  message: string;
  source: IWidget<any> | string;
}

export type PlayerActionsTypes = typeof PlayerActions extends {
  [key: string]: (...args: any[]) => infer U;
}
  ? U
  : never;

export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  PlayerStoreType,
  unknown,
  PlayerActionsTypes
>;

export type GetPlayerActionType<T extends keyof typeof PlayerActions> = ReturnType<
  typeof PlayerActions[T]
>;

/**       */
export interface IContextParam {
  /**     */
  name: string;
  /**   */
  title?: string;
  /**   */
  type: TEPropRsType;
  /**      */
  required: boolean;
  /**       */
  default?: string;
}

/**   */
export interface ISection {
  /**    rowNumber */
  rowNumbers: number[];
  /**   */
  name: ISectionName;
  /**   /  */
  isCollapsible: boolean;
  /**   */
  isExpanded: ISectionExpandLink | ISectionExpandBool;
}

/**   */
interface ISectionName {
  type: TESection;
  value: string;
}

interface ISectionExpandLink {
  type: TLowercase<EPropRsType.LINK>;
  value: string;
}

interface ISectionExpandBool {
  type: TLowercase<EPropRsType.BOOLEAN>;
  value: boolean;
}

export interface ISectionComponentProps {
  /**   */
  title: string;
  /**   */
  children: React.ReactElement;
  /**     */
  isCollapsible: boolean;
  /**     (/) */
  defaultExpanded: boolean;
  /**      */
  hasError: boolean;
}
