/**
 *   .
 *
 * @param {IBlockRouter} block  .
 * @param {Interpreter} interpreter  .
 *
 * @returns {string} PL     
 */
function playRouteBlock(block: IBlockRouter, interpreter: Interpreter): string {
  let nextBlockId;
  // [].some, ..        
  const routeFound = block.routes.some(route => {
    try {
      const res: any = interpreter.interpret(route.expression as IToken);
      if (typeof res === 'boolean' && res === true) {
        nextBlockId = route.nextId;
        return true;
      }
      return false;
    } catch (error) {
      error.nextId = route.nextId;
      throw error;
    }
  });
  //        -    (      )
  if (!routeFound) {
    nextBlockId = block.nextId;
  }
  return nextBlockId;
}

/**
 *  - .
 *
 * @param {Object} config
 * @param {string} config.blockId Id 
 * @param {string} config.scriptName      .
 */
const playScriptBlockByIdAction =
  ({ blockId, scriptName }: IPlayScriptBlockByIdPayload): AppThunk =>
  (dispatch, getState) => {
    const {
      script: { interpreter, script },
    }: IScriptPlayerStore = getState()[scriptName];

    const { blocks } = script;

    const block = getBlockById(blockId, blocks);

    if (!block) {
      return dispatch(
        PlayerActions.playerErrorAction(
          scriptName,
          preparePlayerErrorExpected({
            text: '   ,    .',
          }),
        ),
      );
    }

    //     store  .
    dispatch(PlayerActions.innerSwitchToBlockAction(scriptName, block.id));

    switch (block.type) {
      case EBlockType.START:
        return dispatch(
          playScriptBlockByIdAction({
            scriptName,
            blockId: (block as IBlockStart).nextId,
          }),
        );

      case EBlockType.CALC:
        try {
          const diff: IPlayerContext = playCalcBlock(block as IBlockCalculation, interpreter);

          dispatch(PlayerActions.updateContextDiffAction(scriptName, diff, false));

          return dispatch(
            playScriptBlockByIdAction({
              scriptName,
              blockId: (block as IBlockCalculation).nextId,
            }),
          );
        } catch (exception) {
          return dispatch(
            PlayerActions.calculateErrorAction(
              scriptName,
              prepareCalculateErrorExpected({
                title: '     .',
                message: exception.message,
                stack: exception.stack,
                blockId: block.id,
                additionalId: exception.contextName,
              }),
            ),
          );
        }

      case EBlockType.ROUTER:
        try {
          const nextBlockId: string | null =
            playRouteBlock(block as IBlockRouter, interpreter) || null;

          return dispatch(playScriptBlockByIdAction({ scriptName, blockId: nextBlockId }));
        } catch (exception) {
          return dispatch(
            PlayerActions.calculateErrorAction(
              scriptName,
              prepareCalculateErrorExpected({
                title: '   .',
                message: exception.message,
                stack: exception.stack,
                blockId: block.id,
                additionalId: exception.nextId,
              }),
            ),
          );
        }

      case EBlockType.MONITOR:
        if ((block as IBlockBusinessMonitoring).active) {
          // TODO:   ,   
          // sendBusinessMonitoring({
          //     pid,
          //     history: businessMonitoringHistory,
          //     ...scriptIdentifier,
          // }).then(() => dispatch(clearBusinessMonitoringHistoryAction()));
        }

        return dispatch(
          playScriptBlockByIdAction({
            scriptName,
            blockId: (block as IBlockBusinessMonitoring).nextId,
          }),
        );

      case EBlockType.STOP:
        // TODO:   ,   
        // sendBusinessMonitoring({
        //     pid,
        //     history: businessMonitoringHistory,
        //     ...scriptIdentifier,
        // }).then(() => dispatch(clearBusinessMonitoringHistoryAction()));
        break;

      case EBlockType.SCREEN:
        dispatch(PlayerActions.onStartShowScreenAction(scriptName));
        break;

      default:
        return dispatch(
          PlayerActions.blockErrorsAction(scriptName, [
            prepareBlockErrorExpected({
              title: '    .',
              text: '         .',
            }),
          ]),
        );
    }
  };

/**
 *  .
 *
 * @param {Object} config
 * @param {IPlayerContext} config.playerContext  
 * @param {string} config.scriptName      .
 */
const restartScript =
  ({ playerContext, scriptName }: IRestartScriptPayload): AppThunk =>
  (dispatch, getState) => {
    try {
      const storeStore: IScriptPlayerStore = getState()[scriptName];
      const scriptData = storeStore.script;

      if (scriptData.status !== EStatus.IDLE) {
        const startBlock = scriptData.script.blocks.find(block => block.type === EBlockType.START);

        if (startBlock) {
          dispatch(PlayerActions.resetScriptAction(scriptName, playerContext));

          dispatch(
            playScriptBlockByIdAction({
              scriptName,
              blockId: (startBlock as IBlockStart).id,
            }),
          );
        } else {
          dispatch(
            PlayerActions.playerErrorAction(
              scriptName,
              preparePlayerErrorExpected({ text: '     .' }),
            ),
          );
        }
      }
    } catch (e) {
      if (!(e as IError).errorType) {
        /**
         * TODO:    ,         +
         *  ,    ,       js     
         * (-         2 ).
         */
        prepareAsyncErrorExpected(e);
      }
    }
  };

/**
 *  .
 *
 * @param {Object} config
 * @param {IPlayerContext} config.script 
 * @param {IPlayerContext} config.startContext  
 * @param {string} config.scriptName      .
 */
export const restartAndRunScriptAction =
  ({ script, startContext, scriptName }: IRestartAndRunScriptPayload): AppThunk =>
  dispatch => {
    //   ,  
    dispatch(PlayerActions.startScriptLoadAction(scriptName));

    let convertedScript: IScriptDetails;

    try {
      convertedScript = convertScriptFromServer(script);

      let playerContext = {
        //    (      )
        ...initPlayerContext(convertedScript.blocks),
        ...startContext,
      };

      //         
      if (convertedScript.inputContext) {
        playerContext = getValidatedInputContext(playerContext, convertedScript.inputContext);
      }

      //   
      dispatch(PlayerActions.addScriptDataAction(scriptName, convertedScript, playerContext));

      dispatch(restartScript({ playerContext, scriptName }));
    } catch (err) {
      dispatch(
        PlayerActions.playerErrorAction(
          scriptName,
          prepareCalculateErrorExpected({
            title: err.title,
            message: err.message,
            stack: err.stack,
          }),
        ),
      );
    }
  };
/**
 *      .
 *
 * @param {string} scriptName      .
 */
export const goNextAction =
  (scriptName: string): AppThunk =>
  (dispatch, getState) => {
    const {
      script: {
        currentBlockId,
        script: { blocks },
      },
    }: IScriptPlayerStore = getState()[scriptName];

    const nextScreenBlock = getBlockById(currentBlockId, blocks) as IBlockScreen;

    dispatch(
      nextScreenBlock
        ? playScriptBlockByIdAction({ scriptName, blockId: nextScreenBlock.nextId })
        : PlayerActions.playerErrorAction(
            scriptName,
            preparePlayerErrorExpected({ text: '      .' }),
          ),
    );
  };

/**
 *     .
 *
 * @param {string} scriptName      .
 * @param {string} blockId    .
 * @param {boolean} saveContext     .
 */
export const switchToBlock =
  (scriptName: string, blockId: string, saveContext: boolean = true): AppThunk =>
  dispatch => {
    if (!saveContext) {
      dispatch(PlayerActions.resetScreenContextAction(scriptName));
    }

    dispatch(playScriptBlockByIdAction({ blockId, scriptName }));
  };

/**
 *    .
 *
 * @param {string} phaseName   .
 * @param {string} scriptName      .
 */
export const switchToPhase =
  (phaseName: string, scriptName: string): AppThunk =>
  (dispatch, getState) => {
    const {
      script: {
        script: { blocks, phases },
      },
    }: IScriptPlayerStore = getState()[scriptName];

    if (phases.indexOf(phaseName) === -1) {
      preparePlayerErrorExpected({
        text: `    '${phaseName}'   ,  : ${phases}`,
      });
      return;
    }

    const block = getPhaseStartBlock(phaseName, blocks);

    dispatch(playScriptBlockByIdAction({ blockId: block.id, scriptName }));
  };

/**
 *      
 *
 * @param {string} scriptName      .
 * @param {IHistoryContext} savedHistory    .
 */
export const restoreScriptHistoryAction =
  (scriptName: string, savedHistory: IHistoryContext[]): AppThunk =>
  dispatch => {
    if (!savedHistory?.length) return;

    const { playerContext, blockId: currentBlockId } = savedHistory[savedHistory.length - 1];

    const history: IHistoryContext[] = savedHistory.slice(0, savedHistory.length - 1);

    dispatch(
      PlayerActions.restorePlayerHistoryAction(scriptName, {
        playerContext,
        currentBlockId,
        history,
      }),
    );
  };

/**
 *      
 *
 * @param {string} scriptName      .
 * @param {Function} saveScriptHistoryFunction    
 */
export const saveScriptHistoryAction =
  (scriptName: string, saveScriptHistoryFunction: (history: IHistoryContext[]) => void): AppThunk =>
  (_, getState) => {
    const {
      script: { history, playerContext, currentBlockId, visibleScreenBlock },
    }: IScriptPlayerStore = getState()[scriptName];

    const savedHistory: IHistoryContext[] = [
      ...history,
      {
        blockId: currentBlockId,
        blockType: visibleScreenBlock.type,
        playerContext: { ...playerContext },
      },
    ];

    saveScriptHistoryFunction(savedHistory);
  };

/**
 *       
 *
 * @param {string} scriptName      .
 * @param {IMarketPlace[]} marketPlaceList .
 */
export const fetchMarketPlaceListAction =
  (scriptName: string, marketPlaceList: IMarketPlace[]): AppThunk =>
  async dispatch => {
    const results = await fetchLoadMarketPlaceApi(marketPlaceList);

    const updateResults = results.map((widgetsDataPromise, index) => ({
      ...widgetsDataPromise,
      name: marketPlaceList[index].name,
      baseUrl: marketPlaceList[index].baseUrl,
    }));

    const fulfilledResult = updateResults.filter(({ status }) => status === 'fulfilled');
    const rejectedResult = updateResults.filter(({ status }) => status === 'rejected');

    if (rejectedResult.length > 0) {
      rejectedResult.forEach(item => {
        console.error(
          `   fetch   "${item.name}"   "${item.baseUrl}".`,
        );
      });
    }

    const customWidgetMap: Record<string, ICustomWidgetData> = (
      fulfilledResult as IFilteredFulfilledWidgetsData[]
    ).reduce((accumulator, { value: { widgets } }, i) => {
      widgets.forEach(item => {
        const updateVersionArray = item.versions.map(({ version }) => ({
          version,
          baseUrl: fulfilledResult[i].baseUrl,
        }));

        if (item.name in accumulator) {
          accumulator[item.name] = {
            ...accumulator[item.name],
            versions: [...accumulator[item.name].versions, ...updateVersionArray],
          };
        } else {
          accumulator[item.name] = {
            ...item,
            versions: updateVersionArray,
          };
        }
      });

      return accumulator;
    }, {});
    dispatch(PlayerActions.setCustomWidgetMapAction(scriptName, customWidgetMap));
  };

/**
 *    
 *
 * @param {string} scriptName      .
 * @param {IMarketPlace[]} [marketplaces]  .
 */
export const validateMarketPlaceWidgetsAction =
  (scriptName: string, marketplaces?: IMarketPlace[]): AppThunk =>
  (dispatch, getState) => {
    const {
      customWidgetMap,
      script: { script: { blocks } = {} },
    }: IScriptPlayerStore = getState()[scriptName];

    const screenBlocks =
      (blocks?.filter(b => b.type === EBlockType.SCREEN) as IBlockScreen[]) || [];

    const notFoundCustomWidgetsList = [];

    screenBlocks.forEach(({ widgets }) => {
      widgets.forEach(widget => {
        if (
          'componentType' in widget &&
          widget['componentType'] === EComponentType.MARKETPLACE &&
          !customWidgetMap?.[widget['component']]
        ) {
          notFoundCustomWidgetsList.push(widget['component']);
        }
      });
    });

    if (notFoundCustomWidgetsList.length) {
      const title = `${
        marketplaces?.length
          ? '     :'
          : '     .    :'
      } ${notFoundCustomWidgetsList.join(', ')}`;

      let message = '';

      typeof customWidgetMap === 'object' &&
        Object.keys(customWidgetMap).length &&
        (message = `  : ${Object.keys(customWidgetMap).join(', ')}`);

      dispatch(
        PlayerActions.playerErrorAction(
          scriptName,
          prepareCalculateErrorExpected({
            title,
            message,
          }),
        ),
      );
    }
  };
