import React from 'react';
import { Provider } from 'react-redux';

import {
  PlayerActions,
  restoreScriptHistoryAction,
  restartAndRunScriptAction,
} from '_actions/PlayerActions';
import { IHistoryContext, IPlayerProps, IRestartAndRunScriptPayload } from '_app/Models';

// player store.
import store from '../store/store';

/**
 *       .
 *
 * @param {string} scriptName      .
 */
export const clearScriptData = (scriptName: string) =>
  store.dispatch(PlayerActions.clearScriptDataAction(scriptName));

/**
 *      
 *
 * @param {string} scriptName      .
 * @param {IHistoryContext} savedHistory    .
 */
export const restoreScriptHistory = (scriptName: string, savedHistory: IHistoryContext[]) => {
  restoreScriptHistoryAction(scriptName, savedHistory)(store.dispatch, store.getState, null);
};

/**
 *  .
 *
 * @param {IRestartAndRunScriptPayload} data
 * @param {IPlayerContext} data.script 
 * @param {IPlayerContext} data.startContext  
 * @param {string} data.scriptName      .
 */
export const restartAndRunScript = (data: IRestartAndRunScriptPayload) => {
  restartAndRunScriptAction(data)(store.dispatch, store.getState, null);
};

/**
 * HOC     
 */
export const withStore = Component => (props: IPlayerProps) =>
  (
    <Provider store={store}>
      <Component {...props} />
    </Provider>
  );

/**
 *    
 *
 * @param {string} scriptName      .
 */
export const getCurrentContext = (scriptName: string) => {
  return store.getState()[scriptName].script.playerContext;
};
