const less = require('less');

/**
 *    ,      .
 *
 * @param {IBlockBase[]} blocks .
 */
export function initPlayerContext(blocks: IBlockBase[]): IPlayerContext {
  let initContext: IPlayerContext = {};
  //      .
  function fillContext(element: IWithContextName) {
    const value = get(initContext, element.contextName);
    if (element.contextName && isUndefined(value)) {
      set(initContext, element.contextName, null);
    }
  }

  //    ,   .
  blocks.forEach((block: IBlockBase) => {
    block.type === EBlockType.SCREEN && (block as IBlockScreen).widgets.forEach(fillContext);
    block.type === EBlockType.CALC &&
      (block as IBlockCalculation).calculations.forEach(fillContext);
  });

  return initContext;
}

/**
 *   c Big.js.
 *
 * @param {IPlayerContext} context  .
 */
export const cloneContext = (context: IPlayerContext) => cloneContextSpruce(context);

/**
 *      .
 *
 * @param {string} blockId  .
 * @param {IBlockBase[]} blocks  .
 */
export const getBlockById = (blockId: string, blocks: IBlockBase[]): IBlockBase =>
  blocks?.find(blockItem => blockItem.id === blockId);

/**
 *     .
 *
 * @param {string} phaseName  .
 * @param {IBlockBase[]} blocks  .
 */
export const getPhaseStartBlock = (phaseName: string, blocks: IBlockBase[]): IBlockBase =>
  blocks.filter(b => b.phase === phaseName && b.phaseStart)[0];

/**
 *         .
 *
 * @param {string} name  .
 */
export const isWidgetWithBlockSwitch = (name: EDefaultWidget | string): boolean =>
  WIDGETS_WITH_BLOCK_SWITCH.indexOf(name as EDefaultWidget) >= 0;

/**
 *     Big.js
 *
 * @param {any} value  .
 */
export const checkIsBigDecimal = (value: any): boolean => isBigDecimal(value);

/**
 *         .
 *
 * @param {string} widgetName  .
 */
export const isKnownWidget = (widgetName: EDefaultWidget | string) => !!WIDGET_MAP[widgetName];

/**
 *      ,   .
 *
 * @param {Function} executeCallback  .
 * @param {Function} errorAsyncCallback   .
 * @param {Function} [errorSyncCallback]   .
 */
export function executeAsync(
  executeCallback: () => void,
  errorAsyncCallback: (error: Error) => void,
  errorSyncCallback?: (error: Error) => void,
) {
  try {
    executeCallback();
  } catch (error) {
    errorSyncCallback && errorSyncCallback(error);
    // BPSBSCP-510    ,    render     .
    setTimeout(() => {
      errorAsyncCallback(error);
    }, 0);
  }
}

/**
 *    .
 *
 * @param {any} value .
 */
export function getClass(value: any): string {
  if (isUndefined(value) || isNull(value) || isNaN(value)) {
    return `${value}`;
  }

  if (checkIsBigDecimal(value)) {
    return 'number';
  }

  return Object.prototype.toString
    .call(value)
    .match(/^\[object\s(.*)\]$/)[1]
    .toLowerCase();
}

/*   ,  ,       .
 *     ,  null.
 *
 * @param {IScriptStore} scriptStore   .
 * @param {string} currentBlockId    .
 * @param {IInterpreter} interpreter  .
 */
export function prepareVisibleBlock(
  scriptStore: IScriptStore,
  currentBlockId: string,
  interpreter: IInterpreter,
): {
  visibleScreenBlock: IBlockScreen;
  scriptCalculateErrors: IScriptCalculateError[];
} {
  const {
    script: { blocks },
    scriptCalculateErrors: scriptCalculateErrorsOld,
  } = scriptStore;

  const currentBlock = getBlockById(currentBlockId, blocks) as IBlockScreen;
  let errorsBlockFromDiff: { [rowNumber: number]: IErrorExceptions } = {};
  let visibleScreenBlock: IBlockScreen = null;
  let scriptCalculateErrors: IScriptCalculateError[] = scriptCalculateErrorsOld;

  //   ,  ,       .
  if (currentBlock && currentBlock.type === EBlockType.SCREEN) {
    //   visibility   .
    const widgets = currentBlock.visibility
      ? currentBlock.widgets.filter(widget => {
          if (currentBlock.visibility[widget.rowNumber]) {
            try {
              return !!interpreter.interpret(currentBlock.visibility[widget.rowNumber] as IToken);
            } catch (e) {
              //    ,       .
              errorsBlockFromDiff[widget.rowNumber] = {
                title: '    .',
                message: e.message,
                stack: e.stack,
                blockId: currentBlock.id,
                additionalId: widget.rowNumber.toString(),
              };

              return false;
            }
          } else {
            return true;
          }
        })
      : currentBlock.widgets;

    visibleScreenBlock = {
      ...currentBlock,
      widgets,
    };

    const errorsInVisibility = valuesIn<IErrorExceptions>(errorsBlockFromDiff).map(
      prepareCalculateErrorExpected,
    );

    if (errorsInVisibility.length > 0) {
      scriptCalculateErrors = scriptCalculateErrorsOld.concat(errorsInVisibility);
    }
  }

  return {
    visibleScreenBlock,
    scriptCalculateErrors,
  };
}
/**
 *       .
 *
 * @param {any} value 
 */
export const canBeEmptyProp = (value: any) => !(isNull(value) || isUndefined(value));

/**
 *      .
 *
 * @param {T[]} arrays  .
 * @param {T[]} newArr   .
 * @param {string[]} exclude   .
 */
export function concatUniqArray<T>(arrays: T[], newArr: T[], exclude: string[]) {
  return newArr.reduce((newArray, elem) => concatUniq(newArray, elem, exclude), arrays);
}

/**
 *       .
 *
 * @param {IErrorBase[]} arrays  .
 * @param {IErrorBase[]} newArr   .
 */
export function concatUniqArrayErrors<T>(arrays: T[], newArr: T[]) {
  return concatUniqArray<T>(arrays, newArr, ['dateTime']);
}

/**
 *      .
 *
 * @param {T[]} array  .
 * @param {T} addElem  .
 * @param {string[]} exclude   .
 */
export function concatUniq<T>(array: T[], addElem: T, exclude: string[]) {
  const alreadyInArray = array.some(elem =>
    isEqual(omit<any>(elem, exclude), omit<any>(addElem, exclude)),
  );
  return alreadyInArray ? array : array.concat(addElem);
}

/**
 *      .
 *
 * @param {Date} [date]    .
 *   ,  dayjs  new Date().
 */
export const formatDate = (date?: Date) => dayjs(date).format('DD/MM/YY HH:mm:ss:SSS UTC Z');

/**
 *       
 *
 * @param {string} emailString  
 * @returns {boolean}  
 */
export function validateEmail(emailString: string): boolean {
  const re = /^[-_.+A-z-0-9]+@{1}[-_.A-z-0-9]+\.{1}[A-Za-z--]{2,10}$/;
  const result = emailString && emailString.match(re) && emailString.match(re).length === 1;
  return Boolean(result);
}

/**
 *   .
 *
 * @param {IBlockCalculation} block  .
 * @param {Interpreter} interpreter  .
 *
 * @returns {IPlayerContext}   .
 */
export function playCalcBlock(block: IBlockCalculation, interpreter: Interpreter): IPlayerContext {
  const diff = {};
  block.calculations.forEach((calculation: ICalculation) => {
    try {
      const calcResult = interpreter.interpret(calculation.expression as IToken);
      interpreter.updateContextVariable(calculation.contextName, calcResult);
      diff[calculation.contextName] = calcResult;
    } catch (error) {
      error.contextName = calculation.contextName;
      throw error;
    }
  });
  return diff;
}

/**
 *          
 *
 * @param {IPlayerContext} playerContext   .
 * @param {IContextParam[]} inputContext    .
 *
 * @returns {IPlayerContext|never}     .
 */
export function getValidatedInputContext(
  playerContext: IPlayerContext,
  inputContext: IContextParam[],
): IPlayerContext | never {
  let resultContext = cloneDeep(playerContext);
  const requiredContextErrors: string[] = [];

  inputContext.forEach(({ required, name, default: defaultValue, type }): void => {
    //     ,  
    if (resultContext[name] != null) return;

    //   ,  
    if (required) {
      requiredContextErrors.push(name);
      return;
    }

    //  defaultValue   ,  ,  null
    resultContext[name] = defaultValue ? convertSimpleValue(defaultValue, type) : null;
  });

  if (requiredContextErrors.length) {
    throw new CustomTitleError(
      '    ',
      ` ${requiredContextErrors.length === 1 ? '' : ''} ${requiredContextErrors.join(
        ', ',
      )}      `,
    );
  }

  return resultContext;
}

/**
 *       
 *
 * @param {IWidget} widget .
 * @param {string} id id   style
 */
export function addInlineStyles(widget: IWidget<any>, id: string) {
  const head = document.querySelector('head');
  const elementStyle = document.createElement('style');
  elementStyle.id = id;
  const css = `.${widget.component}_${widget?.index} { ${widget.inlineStyles} }`;

  //    CSS  LESS
  less.render(css, function (error, css) {
    if (error) {
      console.warn('   CSS:', error.message);
    } else {
      elementStyle.innerHTML = css.css;
      head.appendChild(elementStyle);
    }
  });
}

/**
 *   
 *
 * @param {string} id id  style
 */
export function removeInlineStyles(id: string) {
  const elementStyle = document.getElementById(id);

  if (elementStyle) {
    elementStyle.remove();
  }
}

